.social {
  margin-top: auto;
  margin-left: auto;
  margin-bottom: 41px;

  .social__list {
    @include reset-ul;

    li:first-child {
      padding-bottom: 12px;
    }

    li:nth-child(2) {
      padding-bottom: 11px;
    }
  }
}

.social__link {
  font-family: $roboto-condensed;
  @include font(12, 14);
  text-transform: uppercase;

  color: $white;
}
